nav{position:fixed;left:0;width:100%;padding:20px 10px;background:#fff;z-index:2;-webkit-box-shadow:0 5px 5px rgba(0,0,0,0.1);box-shadow:0 5px 5px rgba(0,0,0,0.1)}.nav__item{padding:0 20px 0 0;text-transform:uppercase;color:#000;text-decoration:none}.nav__item.active{text-decoration:underline}.nav__item_cart{float:right;padding-right:30px;position:relative}.nav__item_cart svg{position:absolute;top:50%;right:0;margin-top:-15px;width:25px;height:25px}.product__list{font-size:0;padding-top:70px;text-align:center}.product__case{display:inline-block;vertical-align:top;width:24%;min-width:300px;padding:40px 20px 30px;margin-bottom:1%;border:1px solid rgba(0,0,0,0.2);cursor:pointer;position:relative;text-align:left}.product__case:before{content:'';width:50px;height:50px;border:25px solid transparent;border-right-color:#2e2e2e;border-bottom-color:#2e2e2e;position:absolute;bottom:0;right:0;-webkit-transition:-webkit-transform .3s;transition:-webkit-transform .3s;-o-transition:transform .3s;transition:transform .3s;transition:transform .3s, -webkit-transform .3s;-webkit-transform-origin:right bottom;-ms-transform-origin:right bottom;transform-origin:right bottom;-webkit-transform:scale(0);-ms-transform:scale(0);transform:scale(0);z-index:1}.product__case:hover:before{-webkit-transform:scale(1);-ms-transform:scale(1);transform:scale(1)}.product__case.mark:before{-webkit-transform:scale(1);-ms-transform:scale(1);transform:scale(1);border-right-color:#79aaff;border-bottom-color:#79aaff}.product__case:not(:nth-child(n+5)){margin-right:1%}.product__name{font-size:36px;font-weight:200;padding-bottom:40px}.product__description{font-size:14px;line-height:1.7;width:55%}.product__photo{position:absolute;bottom:0;right:0;overflow:hidden}.product__photo img{min-width:150px;border-radius:50%;margin:0 -30px -30px 0}.title{font-size:36px;font-weight:300;text-transform:uppercase;padding:20px 0 40px;text-align:left}.price{font-size:18px;font-weight:500;padding:15px 0 0}.cart{padding-top:70px}.cart__field{font-size:0;border:1px solid rgba(0,0,0,0.2);margin-bottom:10px;padding:10px 20px;width:100%;-webkit-transform-origin:right center;-ms-transform-origin:right center;transform-origin:right center;-webkit-transition:width .5s;-o-transition:width .5s;transition:width .5s}.cart__field_removed{width:50%;margin-left:auto}.cart__name{text-transform:uppercase;font-size:16px;display:inline-block;vertical-align:middle;width:30%;min-width:180px;padding:20px 0}.cart__pict{display:inline-block;vertical-align:middle;width:30%}.cart__pict img{width:60px;border-radius:50%}.cart-content{font-size:12px;color:#ff0000;font-weight:500;border-radius:50%;background:#fff;position:absolute;top:0;right:0;width:15px;text-align:center;margin:-9px 2px 0 0;z-index:1;-webkit-transform:scale(1.7);-ms-transform:scale(1.7);transform:scale(1.7);opacity:0;-webkit-transition:opacity .3s, -webkit-transform .3s;transition:opacity .3s, -webkit-transform .3s;-o-transition:transform .3s, opacity .3s;transition:transform .3s, opacity .3s;transition:transform .3s, opacity .3s, -webkit-transform .3s}.cart-content.show{-webkit-transform:scale(1);-ms-transform:scale(1);transform:scale(1);opacity:1}.cart__price{font-size:18px;width:30%;min-width:170px;display:inline-block;vertical-align:middle}.cart__count{display:inline-block;vertical-align:middle}.cart__count span{display:inline-block;vertical-align:middle;text-align:center;width:42px;padding:10px;line-height:36px;font-size:20px}.cart__trigger{position:relative;width:10px;height:40px;display:inline-block;vertical-align:middle}.cart__trigger [data-increase],.cart__trigger [data-decrease]{padding:0;position:absolute;left:0;width:14px;height:14px;border:7px solid transparent;cursor:pointer;-webkit-transition:-webkit-transform .2s;transition:-webkit-transform .2s;-o-transition:transform .2s;transition:transform .2s;transition:transform .2s, -webkit-transform .2s}.cart__trigger [data-increase]{top:0;border-bottom-color:#000}.cart__trigger [data-decrease]{bottom:0;border-top-color:#c1c1c1}.cart__trigger [data-decrease].active{border-top-color:#000}.cart__trigger [data-increase]:hover{-webkit-transform:scale(1.1) translateY(-1px);-ms-transform:scale(1.1) translateY(-1px);transform:scale(1.1) translateY(-1px)}.cart__trigger [data-decrease].active:hover{-webkit-transform:scale(1.1) translateY(1px);-ms-transform:scale(1.1) translateY(1px);transform:scale(1.1) translateY(1px)}.cart__button{display:inline-block;vertical-align:middle;width:16px;height:16px;margin-left:7%;position:relative;cursor:pointer}.cart__button:before,.cart__button:after{content:'';width:100%;height:2px;border-radius:2px;background:#000;position:absolute;top:50%;left:0}.cart__button:before{-webkit-transform:rotate(45deg);-ms-transform:rotate(45deg);transform:rotate(45deg)}.cart__button:after{-webkit-transform:rotate(-45deg);-ms-transform:rotate(-45deg);transform:rotate(-45deg)}.cart__button:hover:before,.cart__button:hover:after{background:#999999;-webkit-box-shadow:0 0 5px #7F7F7F;box-shadow:0 0 5px #7F7F7F}.cart__removed{text-transform:uppercase;color:#ff0000;font-size:18px;display:inline-block;vertical-align:middle;width:30%}.cart__notification{padding:20px 0 30px;text-align:center;border:1px solid #bd5454}.cart__notification p{font-size:18px;padding:0 0 15px}.cart__notification a{display:inline-block;width:100%;max-width:280px;padding:15px;text-align:center;text-transform:uppercase;text-decoration:none;color:#823d3d;-webkit-box-shadow:0 0 0 1px #823d3d;box-shadow:0 0 0 1px #823d3d;-webkit-transition:-webkit-box-shadow .3s;transition:-webkit-box-shadow .3s;-o-transition:box-shadow .3s;transition:box-shadow .3s;transition:box-shadow .3s, -webkit-box-shadow .3s}.cart__notification a:hover{-webkit-box-shadow:0 0 5px 1px rgba(130,61,61,0.7);box-shadow:0 0 5px 1px rgba(130,61,61,0.7)}.cart__total{text-align:right;font-size:24px;font-weight:500;text-transform:uppercase;padding:20px 0}
